// https://github.com/GoogleChrome/web-vitals/#send-the-results-to-google-analytics

// const perfURI = "https://my-json-server.typicode.com/amrsekilly/perf-serv/perf-data/";

 const data = {
    createdAt: new Date().toISOString(),
    fcp: 0,
    lcp: 0,
    cls: 0,
    fid: 0,
  };

const calculateFCP = (entryList) => {
  const entries = entryList.getEntries() || [];
  entries.forEach((entry) => {
    if (entry.name === "first-contentful-paint") {
      data.fcp = entry.startTime;
      console.log("Recorded FCP Performance: " + data.fcp);
    }
  });
};

const calculateLCP = (entryList) => {
  const entries = entryList.getEntries() || [];
  entries.forEach((entry) => {
    if (entry.startTime > data.lcp) {
      data.lcp = entry.startTime;
      console.log("Recorded LCP Performance: " + data.lcp);
    }
  });
};

const calculateCLS = (entryList) => {
  const entries = entryList.getEntries() || [];
  entries.forEach((entry) => {
    // @ts-ignore
    if (!entry.hadRecentInput) {
      // to make sure that the shift didn't happen beacuse of a user action. Such as clicking on a dropdown menu for example.
      // @ts-ignore
      data.cls += entry.value;
      console.log("Increased CLS Performance: " + data.cls);
    }
  });
};

const calculateFID = (entryList) => {
  const entries = entryList.getEntries() || [];
  entries.forEach((entry) => {
    // @ts-ignore
    data.fid = entry.processingStart - entry.startTime;
    // startTime is the time the user clicked a button
    // proessingStart is the time the browser started processing the request
    console.log("Recorded FID Performance: " + data.fid);
  });
};

const sendToGoogleAnalytics = ({name, delta, id}) => {
  window.gtag('event', name, {
    event_category: 'Web Vitals',
    event_label: id,
    value: Math.round(name === 'CLS' ? delta * 1000 : delta),
    non_interaction: true,
  });
}


// https://developer.mozilla.org/en-US/docs/Web/API/PerformanceObserver/observe#parameters
const observePerf = () => {
  new PerformanceObserver(calculateFCP).observe({
    type: "paint",
    buffered: true,
  });

  new PerformanceObserver(calculateLCP).observe({
    type: "largest-contentful-paint",
    buffered: true,
  });

  new PerformanceObserver(calculateCLS).observe({
    type: "layout-shift",
    buffered: true,
  });

  new PerformanceObserver(calculateFID).observe({
    type: "first-input",
    buffered: true,
  });

  document.addEventListener("visibilitychange", function logData() {
    if (document.visibilityState === "hidden") {
      data.createdAt = new Date().toISOString();

      // const body = JSON.stringify(data)

      sendToGoogleAnalytics({name: 'FCP', delta: data.fcp, id: data.createdAt})
      sendToGoogleAnalytics({name: 'LCP', delta: data.lcp, id: data.createdAt})
      sendToGoogleAnalytics({name: 'FID', delta: data.fid, id: data.createdAt})
      sendToGoogleAnalytics({name: 'CLS', delta: data.cls, id: data.createdAt})

      // navigator.sendBeacon && navigator.sendBeacon(perfURI, body);
      console.log("Sending performance:", data);
    }
  }); 
};

setTimeout(() => {
  (function (ready) {
  if (typeof window !== 'undefined' && (
    document.readyState === "complete" ||
    document.readyState === "interactive")
  ) {
    ready();
  } else {
    document.addEventListener("readystatechange", function () {
      if (document.readyState === "complete") {
        ready();
      }
    });
  }
})(observePerf);
}, 1000);


